import * as React from 'react';
import { Collapse, Nav, Navbar, NavbarBrand, NavbarToggler } from 'reactstrap';
import './Assets/header.scss';

export default class Header extends React.Component<
  {},
  { active: string; isNavMenuOpened: boolean }
> {
  observer: IntersectionObserver | null = null;

  navRef: React.RefObject<HTMLDivElement>;

  constructor(props) {
    super(props);

    this.navRef = React.createRef();
    this.state = { active: '', isNavMenuOpened: false };
    this.onSectionClick = this.onSectionClick.bind(this);
    this.toggle = this.toggle.bind(this);
    this.handleDocumentClick = this.handleDocumentClick.bind(this);
  }

  componentDidMount() {
    let options = {
      root: null,
      rootMargin: '0px',
      threshold: [0.1],
    };

    if (window.IntersectionObserver) {
      let callback: IntersectionObserverCallback = (entries, observer) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            this.setState({ active: entry.target.id });
          }
        });
      };

      this.observer = new IntersectionObserver(callback, options);

      setTimeout(() => {
        let sections = document.querySelectorAll('#main > section');
        sections.forEach((section) => this.observer?.observe(section));
      }, 1000);
    }

    document.addEventListener('click', this.handleDocumentClick, true);
  }

  componentWillUnmount() {
    if (this.observer) {
      this.observer.disconnect();
    }

    document.removeEventListener('click', this.handleDocumentClick, true);
  }

  handleDocumentClick(e) {
    const container = this.navRef.current;
    if (
      container != null &&
      e.target !== container &&
      !container.contains(e.target)
    ) {
      this.setState({ isNavMenuOpened: false });
    }
  }

  onSectionClick(event: React.MouseEvent<HTMLAnchorElement>) {
    let sectionID = this.getSectionName(event.target as HTMLAnchorElement);
    let element = document.getElementById(sectionID);

    if (element != null) {
      window.scrollTo({
        top: element.offsetTop,
        behavior: 'smooth',
      });
    }
  }

  private getSectionName(element: HTMLAnchorElement) {
    let section = element.href;
    let index = section.lastIndexOf('#');
    let sectionID = section.substr(index >= 0 ? index + 1 : 0);
    return sectionID;
  }

  toggle() {
    this.setState({ isNavMenuOpened: !this.state.isNavMenuOpened });
  }

  render() {
    return (
      <div className="container" ref={this.navRef}>
        <Navbar
          dark={true}
          expand="md"
          className="navbar-custom fixed-top d-flex text-center"
          id="projectNavbar"
        >
          <NavbarBrand href="#Home" aria-label="Home" id="homeButton">
            <img src="favicon-32x32.png" alt="" height={32} width={32}></img>
          </NavbarBrand>

          <NavbarToggler
            onClick={this.toggle}
            data-toggle="collapse"
            data-target="#navbarNavAltMarkup"
            aria-controls="navbarNavAltMarkup"
            aria-expanded={this.state.isNavMenuOpened}
          />

          <Collapse
            id="navbarNavAltMarkup"
            navbar={true}
            isOpen={this.state.isNavMenuOpened}
          >
            <Nav className="ml-auto text-right pr-3" navbar={true} tag="div">
              <a
                className={`nav-item nav-link ${
                  this.state.active === 'Home' ? 'active' : ''
                } pageLink`}
                href="#Home"
                onClick={this.onSectionClick}
              >
                About Me
              </a>
              <a
                className={`nav-item nav-link ${
                  this.state.active === 'Blogs' ? 'active' : ''
                } pageLink`}
                href="#Blogs"
                onClick={this.onSectionClick}
              >
                Blogs
              </a>
              <a
                className={`nav-item nav-link ${
                  this.state.active === 'Projects' ? 'active' : ''
                } pageLink`}
                href="#Projects"
                onClick={this.onSectionClick}
              >
                Projects
              </a>
            </Nav>
          </Collapse>
        </Navbar>
      </div>
    );
  }
}
