import * as React from 'react';

export default class FocusTrapper extends React.Component<
  { IsCurrentElementLast: boolean },
  {}
> {
  private homeButton;
  private themeButton;

  componentDidMount() {
    this.homeButton = document.getElementById('homeButton');
    this.themeButton = document.getElementById('themeMenu');
  }

  onFocus = (event: React.FocusEvent<HTMLDivElement>) => {
    var firstFocusable = this.homeButton;
    var lastFocusable = this.themeButton;

    if (this.props.IsCurrentElementLast) {
      firstFocusable?.focus();
    } else {
      lastFocusable?.focus();
    }
  };

  render() {
    return <div tabIndex={0} onFocus={this.onFocus}></div>;
  }
}
