import * as React from 'react';
import './Assets/content.scss';

const AboutMe = React.lazy(
  () => import(/* webpackChunkName: 'AboutMe' */ './AboutMe')
);
const Blogs = React.lazy(
  () => import(/* webpackChunkName: 'Blogs' */ './Blogs')
);
const Projects = React.lazy(
  () => import(/* webpackChunkName: 'Projects' */ './Projects')
);

export default class Content extends React.Component<{}, {}> {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <main className="main scroller" id="main" data-offset="0">
        <React.Suspense fallback={<div>Loading...</div>}>
          <AboutMe />
          <Blogs />
          <Projects />
        </React.Suspense>
      </main>
    );
  }
}
