import * as React from 'react';
import {
  Dropdown,
  DropdownItem,
  DropdownMenu,
  DropdownToggle,
} from 'reactstrap';
import { setTheme } from './SetTheme';

export default class ThemeMenu extends React.Component<
  {},
  { isThemeOpen: boolean; currentTheme: string }
> {
  private themeKey: Readonly<string> = 'selectedTheme';

  constructor(props) {
    super(props);
    this.toggleThemeMenuOpen = this.toggleThemeMenuOpen.bind(this);

    // Set theme from local storage
    const currentTheme = localStorage.getItem(this.themeKey);
    if (currentTheme !== null) {
      setTheme(currentTheme === 'Dark Theme');
      this.state = {
        currentTheme,
        isThemeOpen: false,
      };
    } else {
      this.state = {
        currentTheme: 'Dark Theme',
        isThemeOpen: false,
      };
      setTheme(true);
      localStorage.setItem(this.themeKey, 'Dark Theme');
    }

    // this.state = {
    //   currentTheme: 'Light Theme',
    //   isThemeOpen: false,
    // };

    // setTheme(false);
  }

  toggleThemeMenuOpen() {
    this.setState({ isThemeOpen: !this.state.isThemeOpen });
  }

  setSelectedTheme = (isDark) => {
    let selectedTheme = isDark ? 'Dark Theme' : 'Light Theme';
    this.setState({ currentTheme: selectedTheme });
    setTheme(isDark);
    localStorage.setItem(this.themeKey, selectedTheme);
  };

  render() {
    return (
      <div className="dropup">
        <Dropdown
          isOpen={this.state.isThemeOpen}
          toggle={this.toggleThemeMenuOpen}
        >
          <DropdownToggle caret id="themeMenu">
            <svg
              width="1.2em"
              height="1.2em"
              viewBox="0 0 16 16"
              className="bi bi-gear mr-2 mb-1"
              fill="currentColor"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                fillRule="evenodd"
                d="M8.837 1.626c-.246-.835-1.428-.835-1.674 0l-.094.319A1.873 1.873 0 0 1 4.377 3.06l-.292-.16c-.764-.415-1.6.42-1.184 1.185l.159.292a1.873 1.873 0 0 1-1.115 2.692l-.319.094c-.835.246-.835 1.428 0 1.674l.319.094a1.873 1.873 0 0 1 1.115 2.693l-.16.291c-.415.764.42 1.6 1.185 1.184l.292-.159a1.873 1.873 0 0 1 2.692 1.116l.094.318c.246.835 1.428.835 1.674 0l.094-.319a1.873 1.873 0 0 1 2.693-1.115l.291.16c.764.415 1.6-.42 1.184-1.185l-.159-.291a1.873 1.873 0 0 1 1.116-2.693l.318-.094c.835-.246.835-1.428 0-1.674l-.319-.094a1.873 1.873 0 0 1-1.115-2.692l.16-.292c.415-.764-.42-1.6-1.185-1.184l-.291.159A1.873 1.873 0 0 1 8.93 1.945l-.094-.319zm-2.633-.283c.527-1.79 3.065-1.79 3.592 0l.094.319a.873.873 0 0 0 1.255.52l.292-.16c1.64-.892 3.434.901 2.54 2.541l-.159.292a.873.873 0 0 0 .52 1.255l.319.094c1.79.527 1.79 3.065 0 3.592l-.319.094a.873.873 0 0 0-.52 1.255l.16.292c.893 1.64-.902 3.434-2.541 2.54l-.292-.159a.873.873 0 0 0-1.255.52l-.094.319c-.527 1.79-3.065 1.79-3.592 0l-.094-.319a.873.873 0 0 0-1.255-.52l-.292.16c-1.64.893-3.433-.902-2.54-2.541l.159-.292a.873.873 0 0 0-.52-1.255l-.319-.094c-1.79-.527-1.79-3.065 0-3.592l.319-.094a.873.873 0 0 0 .52-1.255l-.16-.292c-.892-1.64.902-3.433 2.541-2.54l.292.159a.873.873 0 0 0 1.255-.52l.094-.319z"
              />
              <path
                fillRule="evenodd"
                d="M8 5.754a2.246 2.246 0 1 0 0 4.492 2.246 2.246 0 0 0 0-4.492zM4.754 8a3.246 3.246 0 1 1 6.492 0 3.246 3.246 0 0 1-6.492 0z"
              />
            </svg>
            <span className="mr-1">Theme</span>
          </DropdownToggle>
          <DropdownMenu>
            <DropdownItem
              onClick={(e) => this.setSelectedTheme(true)}
              active={this.state.currentTheme === 'Dark Theme'}
            >
              <div className="themeItem">
                <svg
                  width="1.2em"
                  height="1.2em"
                  viewBox="0 0 16 16"
                  className="bi bi-moon mr-2 mb-1"
                  fill="currentColor"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    fillRule="evenodd"
                    d="M14.53 10.53a7 7 0 0 1-9.058-9.058A7.003 7.003 0 0 0 8 15a7.002 7.002 0 0 0 6.53-4.47z"
                  />
                </svg>
                Dark Theme
              </div>
            </DropdownItem>
            <DropdownItem
              onClick={(e) => this.setSelectedTheme(false)}
              active={this.state.currentTheme === 'Light Theme'}
            >
              <div className="themeItem">
                <svg
                  width="1.2em"
                  height="1.2em"
                  viewBox="0 0 16 16"
                  className="bi bi-brightness-high mr-2 mb-1"
                  fill="currentColor"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    fillRule="evenodd"
                    d="M8 11a3 3 0 1 0 0-6 3 3 0 0 0 0 6zm0 1a4 4 0 1 0 0-8 4 4 0 0 0 0 8zM8 0a.5.5 0 0 1 .5.5v2a.5.5 0 0 1-1 0v-2A.5.5 0 0 1 8 0zm0 13a.5.5 0 0 1 .5.5v2a.5.5 0 0 1-1 0v-2A.5.5 0 0 1 8 13zm8-5a.5.5 0 0 1-.5.5h-2a.5.5 0 0 1 0-1h2a.5.5 0 0 1 .5.5zM3 8a.5.5 0 0 1-.5.5h-2a.5.5 0 0 1 0-1h2A.5.5 0 0 1 3 8zm10.657-5.657a.5.5 0 0 1 0 .707l-1.414 1.415a.5.5 0 1 1-.707-.708l1.414-1.414a.5.5 0 0 1 .707 0zm-9.193 9.193a.5.5 0 0 1 0 .707L3.05 13.657a.5.5 0 0 1-.707-.707l1.414-1.414a.5.5 0 0 1 .707 0zm9.193 2.121a.5.5 0 0 1-.707 0l-1.414-1.414a.5.5 0 0 1 .707-.707l1.414 1.414a.5.5 0 0 1 0 .707zM4.464 4.465a.5.5 0 0 1-.707 0L2.343 3.05a.5.5 0 1 1 .707-.707l1.414 1.414a.5.5 0 0 1 0 .708z"
                  />
                </svg>
                Light Theme
              </div>
            </DropdownItem>
            {/* <DropdownItem
                  onClick={(e) =>
                    this.setState({ currentTheme: 'High Constrast Theme' })
                  }
                  active={this.state.currentTheme === 'High Constrast Theme'}
                >
                  High Constrast Theme
                </DropdownItem> */}
          </DropdownMenu>
        </Dropdown>
      </div>
    );
  }
}
